.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.25s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.search {
  position: relative;
  padding-top: 148px;
  height: calc(100% - 148px);
}
.search .search-bar-wrap {
  position: absolute;
  top: 0;
  width: 100%;
  min-height: 148px;
  background: #fff;
  z-index: 1;
  transition: all 0.2s;
}
.search .search-bar-wrap ::v-deep .van-icon-search {
  margin-top: 2px;
  margin-left: 4px;
  font-size: 20px;
}
.search .search-bar-wrap ::v-deep .van-icon-clear {
  margin-top: 2px;
  margin-right: -2px;
  font-size: 20px;
}
.search .search-bar-wrap .search-bar {
  position: absolute;
  width: 100%;
  height: 128px;
  top: 26px;
}
.search .search-bar-wrap .search-bar ::v-deep .van-cell {
  line-height: 32px;
}
.search .search-bar-wrap .search-bar ::v-deep .van-cell input {
  display: inline-block;
  opacity: 0;
}
.search .search-bar-wrap .search-bar-word {
  position: absolute;
  top: 70px;
  left: 85px;
  font-size: 0;
  width: 100%;
  max-width: 580px;
  height: 42px;
  border-radius: 8px;
  overflow-x: scroll;
  white-space: nowrap;
}
.search .search-bar-wrap .search-bar-word .placeholder {
  font-size: 28px;
  line-height: 42px;
  color: #adadad;
}
.search .search-bar-wrap .search-bar-word ::v-deep .word {
  display: inline-block;
  color: #fff;
  background: #7bb7e7;
  padding: 6px 8px;
  margin: 0 4px;
  border-radius: 8px;
  font-size: 24px;
  overflow: hidden;
}
.search .search-bar-wrap .search-bar-word ::v-deep .word .text {
  border-right: 1px solid #acd9fd;
  padding-right: 8px;
}
.search .search-bar-wrap .search-bar-word ::v-deep .word .text.no-line {
  border-color: transparent;
}
.search .search-bar-wrap .search-bar-word ::v-deep .word .close {
  display: inline-block;
  width: 24px;
  height: 24px;
  background: url("~@/svg/close.svg");
  background-size: 100%;
}
.search .search-bar-wrap .image-search-mask {
  position: fixed;
  top: 154px;
  width: 100%;
  max-width: 10rem;
  height: calc(100% - 124px);
  box-sizing: border-box;
  background: rgba(0,0,0,0.6);
  transition: all 0.2s;
}
.search .search-bar-wrap .search-history {
  margin-top: 150px;
  margin-bottom: 20px;
  width: 100%;
  padding: 0 6px;
  box-sizing: border-box;
  overflow: hidden;
}
.search .search-bar-wrap .search-history .title-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 40px;
  font-size: 26px;
  margin: 8px 20px;
}
.search .search-bar-wrap .search-history .keyword {
  float: left;
  font-size: 24px;
  padding: 12px 20px;
  background: #eaeaea;
  border-radius: 26px;
  margin: 12px 12px;
  user-select: none;
  white-space: nowrap;
  max-width: 50%;
  overflow: hidden;
  text-overflow: ellipsis;
}
.search .list-wrap {
  position: relative;
  height: 100%;
  overflow-y: scroll;
}
.search .list-wrap >.mask {
  display: none;
}
.search .list-wrap.focus >.mask {
  display: block;
  position: fixed;
  top: 148px;
  width: 100%;
  max-width: 10rem;
  height: calc(100% - 124px);
  box-sizing: border-box;
  background: rgba(0,0,0,0.6);
  transition: all 0.2s;
}
.loading {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.result-list {
  margin: 0 2px;
}
.result-list .card-box {
  display: flex;
  flex-direction: row;
}
.result-list .card-box .column {
  width: 50%;
}
.result-list .card-box .column .image-card {
  max-height: 360px;
  margin: 4px 2px;
}
