




















































.nav {
  width: 90%;
  overflow-x: scroll;
  overflow-y: hidden;
  white-space: nowrap;

  a {
    display: inline-block;
    padding: 12px 20px;
    margin: 8px 6px;
    border-radius: 24px;
    color: #333;
    background: #eee;
    box-sizing: border-box;
    scroll-margin: 120px;

    &.cur {
      background: #f2c358;
    }
  }
}