.tags {
  display: flex;
  flex-direction: column;
}
.tags .tag {
  position: relative;
  float: left;
  width: 33.3%;
}
.tags .tag img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.tags .tag .meta {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  text-align: center;
  color: #fff;
  background: rgba(0,0,0,0.3);
}
.tags .tag .meta .content {
  position: absolute;
  bottom: 10%;
  width: 100%;
}
.tags .tag .meta .content .name {
  font-size: 36px;
  margin: 10px 0;
}
.tags .tag .meta .content .tname {
  font-size: 28px;
  margin: 10px 0;
}
.tags .tag .meta .content .l {
  font-size: 30px;
}
.tags .tag .meta .content .m {
  font-size: 26px;
}
.tags .tag .meta .content .s {
  font-size: 24px;
}
.tags .top .tag {
  height: 600px;
  width: 100%;
}
.tags .bottom {
  display: flex;
}
.tags .bottom .row .tag {
  width: 33.33%;
}
