.image-search {
  position: absolute;
  top: 56px;
  width: 100%;
  z-index: 1;
}
.image-search .open-dialog {
  position: absolute;
  top: 10px;
  right: 46px;
}
.image-search .open-dialog ::v-deep .van-uploader__wrapper--disabled {
  opacity: 1;
}
.image-search .open-dialog .loading {
  margin-top: -8px;
  margin-right: -8px;
  width: 3em;
  height: 3em;
  background: url("~@/svg/loading-1.svg");
  background-size: 100%;
}
.image-search .container {
  position: absolute;
  top: 92px;
  width: 100%;
  background: #fff;
}
.image-search .container > .thumb {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  overflow: hidden;
}
.image-search .container > .thumb::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255,255,255,0);
}
.image-search .container > .thumb img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 110%;
  height: 110%;
  object-fit: cover;
  filter: blur(6px);
}
.image-search .container .result-list {
  position: relative;
  margin: 20px 20px;
  max-height: 50vh;
  overflow-y: scroll;
  border-radius: 12px;
}
.image-search .container .result-list .result {
  position: relative;
  display: flex;
  justify-content: space-between;
  height: 160px;
  margin-top: 20px;
  border-radius: 12px;
  overflow: hidden;
  box-sizing: border-box;
  background: rgba(255,255,255,0.95);
}
.image-search .container .result-list .result:first-of-type {
  margin: 0;
}
.image-search .container .result-list .result .thumb {
  position: relative;
  margin: 0;
  margin-right: 20px;
  width: 30%;
  height: auto;
  object-fit: cover;
}
.image-search .container .result-list .result .meta {
  flex: 1;
  padding: 20px 0;
}
.image-search .container .result-list .result .meta .title {
  font-size: 30px;
  margin-bottom: 10px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 470px;
}
.image-search .container .result-list .result .meta .info {
  font-size: 24px;
  line-height: 36px;
  color: #888;
  max-width: 300px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.image-search .container .result-list .result .similarity {
  position: absolute;
  right: 0;
  width: 180px;
  height: 155px;
  margin-top: 5px;
  font-family: 'Dosis';
  font-size: 60px;
  font-weight: 600;
  line-height: 160px;
  text-align: right;
  color: #555;
  letter-spacing: 2px;
  padding-right: 20px;
}
.image-search .container .result-list .result .low {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255,255,255,0.6);
  pointer-events: none;
}
