






























.image-slide {
  position: relative;
  height: 100%;
  overflow: hidden;

  .slide {
    display: flex;
    height: 100%;
    width: 110%;
    margin-left: -8%;
    display: flex;

    .image {
      width: 26%;
      object-fit: cover;
      clip-path: polygon(25% 0, 100% 0, 75% 100%, 0% 100%);
      margin-right: -6%;
    }
  }
}
