.rank-card .card-box {
  padding: 0 12px;
  height: 365px;
}
.rank-card .card-box .swipe-wrap {
  height: 100%;
  border-radius: 20px;
  overflow: hidden;
}
.rank-card .card-box .swipe-wrap .swipe-item:last-child .image-card {
  margin-right: 0;
}
.rank-card .card-box .swipe-wrap .swipe-item .image-card {
  font-size: 0;
  float: left;
  margin-right: 12px;
  border: 1px solid #ebebeb;
  border-radius: 18px;
  box-sizing: border-box;
}
.rank-card .card-box .swipe-wrap .swipe-item .image-slide {
  border: 1px solid #ebebeb;
  border-radius: 18px;
  box-sizing: border-box;
}
.rank-card .card-box .swipe-wrap .swipe-item .image-slide .link {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #efefef;
}
.rank-card .card-box .swipe-wrap .swipe-item .image-slide .link::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0,0,0,0.6);
}
.rank-card .card-box .swipe-wrap .swipe-item .image-slide .link svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -55%);
  font-size: 20em;
}
.rank-card .card-box .swipe-wrap .swipe-item .image-slide .link div {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, 80%);
  font-size: 34px;
  text-align: center;
  white-space: nowrap;
}
.rank-card .card-box .swipe-wrap .swipe-item.more .rank {
  display: flex;
  height: 100%;
  justify-content: center;
  align-items: center;
}
.daily .artwork-list {
  margin: 0 2px;
}
.daily .artwork-list .card-box {
  display: flex;
  flex-direction: row;
}
.daily .artwork-list .card-box .column {
  width: 50%;
}
.daily .artwork-list .card-box .column .image-card {
  max-height: 360px;
  margin: 4px 2px;
}
