










































.top3 {
  position: relative;
  display: flex;
  margin: 16px;

  .box {
    border-radius: 24px;
    overflow: hidden;
    box-sizing: border-box;

    .image-card {
      height: 100% !important;

      ::v-deep .meta {
        .content {
          .title {
            font-size: 24px;
          }

          .avatar {
            width: 32px;
            height: 32px;
          }

          .author {
            font-size: 20px;
          }
        }
      }
    }
  }

  .l {
    width: 66%;
    height: 480px;
    margin-right: 8px;
  }

  .r {
    display: flex;
    flex-direction: column;
    width: 33%;
    height: 480px;
    margin-left: 8px;

    .t {
      height: 50%;
      margin-bottom: 8px;
    }

    .b {
      height: 50%;
      margin-top: 8px;
    }
  }
}
