.rank {
  padding-top: 70px;
  height: 100%;
  box-sizing: border-box;
}
.rank .loading {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.rank .top {
  position: fixed;
  display: flex;
  justify-content: space-between;
  align-items: center;
  top: 0;
  width: 750px;
  height: 118px;
  padding: 46px 12px 0 12px;
  box-sizing: border-box;
  background: #fff;
  z-index: 1;
}
.rank .top .calendar {
  position: relative;
  width: 60px;
  height: 60px;
  background: url("~@/assets/images/calendar.png") center no-repeat;
  background-size: 100%;
  transform: translateY(-4px);
}
.rank .top .calendar .date {
  position: absolute;
  top: 21.5px;
  left: 55%;
  transform: translateX(-50%);
  color: #666;
  font-family: Dosis;
  font-size: 26px;
  font-weight: 600;
  letter-spacing: 4px;
}
.rank .top ::v-deep .vc-popover-content-wrapper {
  top: 120px !important;
  left: auto !important;
  right: 14px;
  transform: none !important;
}
.rank .top ::v-deep .vc-popover-content-wrapper .vc-popover-caret {
  left: 94% !important;
}
.rank .rank-list {
  margin: 0 2px;
}
.rank .rank-list .card-box {
  display: flex;
  flex-direction: row;
}
.rank .rank-list .card-box .column {
  width: 50%;
}
.rank .rank-list .card-box .column .image-card {
  max-height: 360px;
  margin: 4px 2px;
}
/*# sourceMappingURL=src/views/Rank/index.css.map */