.top-bar {
  position: fixed;
  top: 0;
  padding-top: 60px;
  width: 100%;
  max-width: 10rem;
  background: linear-gradient(to bottom, rgba(0,0,0,0.3), transparent);
  z-index: 99;
}
.top-bar svg {
  font-size: 22px;
  color: #fafafa;
  transform: translateY(10px);
}
