.image-view {
  position: relative;
  min-height: 600px;
  background-color: #fafafa;
}
.image-view.shrink {
  max-height: 1000px;
  overflow: hidden;
}
.image-view.shrink::after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: linear-gradient(to top, #fff, transparent);
}
.image-view.shrink .dropdown {
  position: absolute;
  bottom: 26px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
  filter: drop-shadow(1px 4px 4px rgba(0,0,0,0.5));
  animation: ani-dropdown 2s ease-in-out infinite;
}
@-moz-keyframes ani-dropdown {
  0%, 100% {
    transform: translate(-50%, 0);
  }
  50% {
    transform: translate(-50%, 6px);
  }
}
@-webkit-keyframes ani-dropdown {
  0%, 100% {
    transform: translate(-50%, 0);
  }
  50% {
    transform: translate(-50%, 6px);
  }
}
@-o-keyframes ani-dropdown {
  0%, 100% {
    transform: translate(-50%, 0);
  }
  50% {
    transform: translate(-50%, 6px);
  }
}
@keyframes ani-dropdown {
  0%, 100% {
    transform: translate(-50%, 0);
  }
  50% {
    transform: translate(-50%, 6px);
  }
}
.image-view .image-box {
  position: relative;
  background: #fafafa;
  min-height: 600px;
  max-height: 1000px;
}
.image-view .image-box .image {
  width: 100%;
  height: 100%;
  min-height: 600px;
  max-height: 1000px;
  object-fit: cover;
}
.image-view .image-box .image[lazy='loading'] {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 120px;
  height: 120px;
  min-height: auto;
}
