





















































































































.artwork-meta {
  position: relative;
  padding: 12px 20px;
  margin: 24px 0;

  .mask {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
    overflow: hidden;

    .mask-text {
      width: 100%;
      height: 100%;
    }
  }

  .author-info {
    height: 86px;
    margin: 12px 0;

    .avatar {
      float: left;
      width: 86px;
      height: 86px;
      border-radius: 50%;
      overflow: hidden;
      margin-right: 18px;
    }

    .name-box {
      height: 100%;
      white-space: nowrap;

      .title {
        padding-top: 4px;
        margin-bottom: 8px;
        font-size: 32px;
        overflow: hidden;
        text-overflow: ellipsis;
      }

      .author {
        font-size: 22px;
        color: #9b9b9b;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }
  }

  .date {
    font-size: 24px;
    color: #7c8f99;
    margin: 16px 0;

    .view {
      margin-left: 24px;
      color: #3e7699;

      .icon {
        font-size: 1em;
        margin-right: 0px;
        vertical-align: -0.14em;
      }
    }

    .like {
      margin-left: 24px;
      color: #36a8f5;

      .icon {
        font-size: 0.8em;
        margin-right: 0px;
        vertical-align: baseline;
      }
    }

    .id {
      margin-left: 12px;
    }
  }

  .tag-list {
    color: #3e7699;
    margin: 16px 0;
    overflow: hidden;

    .tag {
      line-height: 42px;
      font-size: 26px;
      float: left;
      margin-right: 10px;

      &.translated {
        font-size: 22px;
        color: #adadad;
        margin-right: 20px;
      }
    }
  }

  .caption {
    font-size: 24px;
    line-height: 32px;
    word-break: break-all;

    ::v-deep a {
      color: #36a8f5;
    }
  }
}
