.author-card {
  padding: 0 14px;
  margin: 24px 0;
}
.author-card .name-bar {
  height: 96px;
  margin: 20px 0;
}
.author-card .name-bar .avatar {
  float: left;
  width: 96px;
  height: 96px;
  border-radius: 50%;
  overflow: hidden;
  margin-right: 18px;
}
.author-card .name-bar .author {
  font-size: 34px;
  line-height: 96px;
  color: #777;
  overflow: hidden;
  text-overflow: ellipsis;
}
.author-card .cell .icon {
  border-radius: 50%;
  vertical-align: middle;
}
.author-card .cell .title {
  font-size: 28px;
}
.author-card .artwork-list-wrap {
  overflow-x: scroll;
  border-radius: 20px;
}
.author-card .artwork-list-wrap .artwork-list {
  display: flex;
}
.author-card .artwork-list-wrap .artwork-list .swiper-slide.image-card-slide {
  width: 40%;
}
.author-card .artwork-list-wrap .artwork-list .swiper-slide.image-slide-slide {
  width: 70%;
}
.author-card .artwork-list-wrap .artwork-list .swiper-slide .image-card {
  height: 330px !important;
  border: 1px solid #ebebeb;
  border-radius: 18px;
  box-sizing: border-box;
  margin-right: 6px;
}
.author-card .artwork-list-wrap .artwork-list .swiper-slide .image-slide {
  height: 330px !important;
  border: 1px solid #ebebeb;
  border-radius: 18px;
  box-sizing: border-box;
}
.author-card .artwork-list-wrap .artwork-list .swiper-slide .image-slide .link {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #efefef;
}
.author-card .artwork-list-wrap .artwork-list .swiper-slide .image-slide .link::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0,0,0,0.6);
}
.author-card .artwork-list-wrap .artwork-list .swiper-slide .image-slide .link svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -55%);
  font-size: 20em;
}
.author-card .artwork-list-wrap .artwork-list .swiper-slide .image-slide .link div {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, 80%);
  font-size: 34px;
  text-align: center;
  white-space: nowrap;
}
.author-card .artwork-list-wrap .artwork-list .swiper-slide.more .rank {
  display: flex;
  height: 100%;
  justify-content: center;
  align-items: center;
}
/*# sourceMappingURL=src/views/Artwork/components/AuthorCard.css.map */