.top3 {
  position: relative;
  display: flex;
  margin: 16px;
}
.top3 .box {
  border-radius: 24px;
  overflow: hidden;
  box-sizing: border-box;
}
.top3 .box .image-card {
  height: 100% !important;
}
.top3 .box .image-card ::v-deep .meta .content .title {
  font-size: 24px;
}
.top3 .box .image-card ::v-deep .meta .content .avatar {
  width: 32px;
  height: 32px;
}
.top3 .box .image-card ::v-deep .meta .content .author {
  font-size: 20px;
}
.top3 .l {
  width: 66%;
  height: 480px;
  margin-right: 8px;
}
.top3 .r {
  display: flex;
  flex-direction: column;
  width: 33%;
  height: 480px;
  margin-left: 8px;
}
.top3 .r .t {
  height: 50%;
  margin-bottom: 8px;
}
.top3 .r .b {
  height: 50%;
  margin-top: 8px;
}
