













































.nav-container {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100px;
  z-index: 10;

  .nav-bar {
    display: flex;
    justify-content: space-between;
    background: #fff;
    /* border-radius: .7rem .7rem 0 0; */
    box-shadow: 0px 0px 20px #dedede;
    width: 100%;
    max-width: 750px;
    height: 100%;
    margin: 0 auto;

    li {
      position: relative;
      color: #777;
      font-size: 22px;
      cursor: pointer;
      width: 20%;
      vertical-align: middle;
      text-align: center;
      margin: 0 10px;

      .icon {
        display: block;
        font-size: 58px;
        margin: 0 auto;
        margin-top: 0.1rem;
        color: #fffdf7;

        &.active {
          color: #F2C358;
        }
      }
    }
  }
}
