.artwork-meta {
  position: relative;
  padding: 12px 20px;
  margin: 24px 0;
}
.artwork-meta .mask {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  overflow: hidden;
}
.artwork-meta .mask .mask-text {
  width: 100%;
  height: 100%;
}
.artwork-meta .author-info {
  height: 86px;
  margin: 12px 0;
}
.artwork-meta .author-info .avatar {
  float: left;
  width: 86px;
  height: 86px;
  border-radius: 50%;
  overflow: hidden;
  margin-right: 18px;
}
.artwork-meta .author-info .name-box {
  height: 100%;
  white-space: nowrap;
}
.artwork-meta .author-info .name-box .title {
  padding-top: 4px;
  margin-bottom: 8px;
  font-size: 32px;
  overflow: hidden;
  text-overflow: ellipsis;
}
.artwork-meta .author-info .name-box .author {
  font-size: 22px;
  color: #9b9b9b;
  overflow: hidden;
  text-overflow: ellipsis;
}
.artwork-meta .date {
  font-size: 24px;
  color: #7c8f99;
  margin: 16px 0;
}
.artwork-meta .date .view {
  margin-left: 24px;
  color: #3e7699;
}
.artwork-meta .date .view .icon {
  font-size: 1em;
  margin-right: 0px;
  vertical-align: -0.14em;
}
.artwork-meta .date .like {
  margin-left: 24px;
  color: #36a8f5;
}
.artwork-meta .date .like .icon {
  font-size: 0.8em;
  margin-right: 0px;
  vertical-align: baseline;
}
.artwork-meta .date .id {
  margin-left: 12px;
}
.artwork-meta .tag-list {
  color: #3e7699;
  margin: 16px 0;
  overflow: hidden;
}
.artwork-meta .tag-list .tag {
  line-height: 42px;
  font-size: 26px;
  float: left;
  margin-right: 10px;
}
.artwork-meta .tag-list .tag.translated {
  font-size: 22px;
  color: #adadad;
  margin-right: 20px;
}
.artwork-meta .caption {
  font-size: 24px;
  line-height: 32px;
  word-break: break-all;
}
.artwork-meta .caption ::v-deep a {
  color: #36a8f5;
}
/*# sourceMappingURL=src/views/Artwork/components/Meta.css.map */