




















































































.image-card {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  background: #fafafa;

  .image {
    width: 100%;
    height: 100%;
    object-fit: cover;

    &[lazy='loading'] {
      width: 100px;
      height: 100px;
    }
  }

  .tag-r18 {
    position: absolute;
    top: 8px;
    left: 6px;
  }

  .layer-num {
    position: absolute;
    top: 4px;
    right: 3px;
    background: rgba(#000, 0.3);
    color: #fff;
    padding: 4px 8px;
    font-size: 20px;
    border-radius: 20px;

    svg {
      vertical-align: bottom;
      margin-right: -2px;
    }
  }

  .meta {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    &::before {
      position: absolute;
      content: '';
      width: 100%;
      height: 100%;
      background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.5) 0%, rgba(255, 255, 255, 0) 100%);
    }

    .content {
      position: absolute;
      bottom: 0;
      width: 100%;
      padding: 18px 14px;
      box-sizing: border-box;
      color: #fff;

      .title {
        font-size: 34px;
        margin: 10px 0;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
      }

      .avatar {
        width: 48px;
        height: 48px;
        margin-right: 8px;
        vertical-align: bottom;
        border-radius: 50%;
        overflow: hidden;
      }

      .author {
        display: inline-block;
        font-size: 30px;
        font-weight: 200;
      }
    }
  }
}
